"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import { toast } from "sonner"

export default function AdminSettingsPage() {
  const [settings, setSettings] = useState({
    siteName: "FiveM Tools V7",
    siteDescription: "Free FiveM Resources",
    maintenanceMode: false,
    registrationEnabled: true,
    uploadEnabled: true,
    forumEnabled: true,
    spinWheelEnabled: true,
    dailyCoins: 100,
    maxUploadSize: 500,
    requireApproval: true,
  })

  const handleSave = async () => {
    try {
      const res = await fetch("/api/admin/settings", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(settings),
      })
      if (res.ok) {
        toast.success("Settings saved")
      }
    } catch (error) {
      toast.error("Failed to save settings")
    }
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold">Site Settings</h1>

      <Card>
        <CardHeader>
          <CardTitle>General Settings</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label>Site Name</Label>
            <Input
              value={settings.siteName}
              onChange={(e) => setSettings({ ...settings, siteName: e.target.value })}
            />
          </div>
          <div>
            <Label>Site Description</Label>
            <Input
              value={settings.siteDescription}
              onChange={(e) => setSettings({ ...settings, siteDescription: e.target.value })}
            />
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Feature Toggles</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center justify-between">
            <Label>Maintenance Mode</Label>
            <Switch
              checked={settings.maintenanceMode}
              onCheckedChange={(checked) => setSettings({ ...settings, maintenanceMode: checked })}
            />
          </div>
          <div className="flex items-center justify-between">
            <Label>Registration Enabled</Label>
            <Switch
              checked={settings.registrationEnabled}
              onCheckedChange={(checked) => setSettings({ ...settings, registrationEnabled: checked })}
            />
          </div>
          <div className="flex items-center justify-between">
            <Label>Upload Enabled</Label>
            <Switch
              checked={settings.uploadEnabled}
              onCheckedChange={(checked) => setSettings({ ...settings, uploadEnabled: checked })}
            />
          </div>
          <div className="flex items-center justify-between">
            <Label>Forum Enabled</Label>
            <Switch
              checked={settings.forumEnabled}
              onCheckedChange={(checked) => setSettings({ ...settings, forumEnabled: checked })}
            />
          </div>
          <div className="flex items-center justify-between">
            <Label>Spin Wheel Enabled</Label>
            <Switch
              checked={settings.spinWheelEnabled}
              onCheckedChange={(checked) => setSettings({ ...settings, spinWheelEnabled: checked })}
            />
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>System Settings</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label>Daily Coins Reward</Label>
            <Input
              type="number"
              value={settings.dailyCoins}
              onChange={(e) => setSettings({ ...settings, dailyCoins: parseInt(e.target.value) })}
            />
          </div>
          <div>
            <Label>Max Upload Size (MB)</Label>
            <Input
              type="number"
              value={settings.maxUploadSize}
              onChange={(e) => setSettings({ ...settings, maxUploadSize: parseInt(e.target.value) })}
            />
          </div>
          <div className="flex items-center justify-between">
            <Label>Require Asset Approval</Label>
            <Switch
              checked={settings.requireApproval}
              onCheckedChange={(checked) => setSettings({ ...settings, requireApproval: checked })}
            />
          </div>
        </CardContent>
      </Card>

      <Button onClick={handleSave} size="lg">
        Save All Settings
      </Button>
    </div>
  )
}
